<template>
  <div>
    <div id="header">
      <header-nav/>
    </div>
    <div
      style="max-width:100%;height:300px;margin:20px 0;background: url('https://guanfan.oss-cn-hangzhou.aliyuncs.com/apk/spiltImg2.jpg') center no-repeat;background-size: cover"></div>
    <div class="clearfix content">
      <el-tabs class="mt20" v-model="activeName" @tab-click="handleClick">
        <el-tab-pane v-for="(item, index) in editableTabs"
                     :key="index"
                     :label="item.title"
                     :name="item.name">

          <ul class="like-list2 clearfix">
            <li class="item" v-for="(item, index) in mainMenuList" :key="index">
              <div class="cover bg-img" :style="{ backgroundImage: 'url(' + item.dtimg + ')' }"
                   @click="navToDetail(item.id)" v-if="item.isvideo">
              </div>
              <div class="cover bg-img" :style="{ backgroundImage: 'url(' + osshost +item.dtimg +listpic+ ')' }"
                   @click="navToDetail(item.id)" v-else>
              </div>
              <div>
                <div>
                  <div class="name text-tips text-cut-two">{{ item.description }}</div>
                  <div class="author">
                    <img :src="imgurl" class="headicon br50" @click="navToIndex(item.userid)" v-if="!item.faceimg">
                    <img :src="osshost + item.faceimg +userface" class="headicon br50" @click="navToIndex(item.userid)" v-if="item.faceimg&&item.isThree==false">
                    <img :src="item.faceimg" class="headicon br50" @click="navToIndex(item.userid)" v-if="item.faceimg&&item.isThree==true">
                    <span class="nickname text-lips" style="max-width: 238px">{{item.nickname}}</span>
                    <span class="right text-xm el-icon-lollipop margin-right-xs block">{{  item.likecount}}</span>
                  </div>
                </div>
              </div>
            </li>
          </ul>
          <infinite-loading @infinite="infiniteHandler" class="mt50 mb60">
            <div slot="spinner"><i class="el-icon-loading"></i>小弟拼命加载中...</div>//加载中的文字
            <div slot="no-more" class="el-icon-food grayColor">已加载完毕!</div>//加载完毕的文字
            <div slot="no-results" class="el-icon-coffee-cup grayColor">暂无数据:(</div>//没有数据的文字
          </infinite-loading>
        </el-tab-pane>
      </el-tabs>
    </div>
    <FooterBottom></FooterBottom>
    <Totop></Totop>
  </div>
</template>

<script>
  import FooterBottom from "@/components/footer.vue";
  import HeaderNav from "@/components/headerNav";
  import Totop from '@/components/totop.vue';
  import InfiniteLoading from 'vue-infinite-loading';
  import {actionList} from '@/api/action'
  import {Message} from "element-ui";

  export default {
    name: "dTList",
    inject: ['reload'],
    components: {
      FooterBottom,
      HeaderNav,
      InfiniteLoading,
      Totop
    },
    data() {
      return {
        editableTabs: [{
          title: '最新',
          name: '1',
        }, {
          title: '最热',
          name: '2',
        }],
        osshost: process.env.UPLOAD_URL,
        listpic: '?x-oss-process=style/list-img',
        userface: '?x-oss-process=style/user-img',
        detailpic: '?x-oss-process=style/detail-img',
        mainMenuList: [],
        activeName: '1',
        page: 0,
        imgurl: '../../static/userIcon.png',
        isThree: false
      }
    },
    methods: {
      handleClick(tab, event) {
        this.activeName = tab.name;
        this.page = 0;
        this.mainMenuList = [];
        this.infiniteHandler()
      },
      infiniteHandler($state) {
        this.page++;
        this.getDtList($state)
      },
      getDtList($state) {
        actionList(this.activeName, this.page).then(response => {
          let res = response.data;
          if (res.success) {
            if (res.result.length !== 0) {
              this.mainMenuList = this.mainMenuList.concat(res.result);
              for(var i=0;i<this.mainMenuList.length;i++){
              	this.mainMenuList[i].isThree = new RegExp('http').test(this.mainMenuList[i].faceimg);
              };
              for (var i = 0; i < this.mainMenuList.length; i++) {
                this.mainMenuList[i].dtimg = this.mainMenuList[i].dtimg.split('|');
                this.mainMenuList[i].dtimg = this.mainMenuList[i].dtimg[0];
              }
              if (this.page != 0) {
                $state.loaded();
              }
            } else {
              $state.complete();
            }
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      },
      navToIndex(id) {
        this.$router.push({path: "/user", query: {id}})
      },
      navToDetail(id) {
        this.$router.push({path: "/dtDetail", query: {id: id}})
      },
    }
  }
</script>

<style scoped>
</style>



